/* ==========================================================================
   Design System
   Expandable Styling
   ========================================================================== */
.o-expandable {
  position: relative;
}
.o-expandable_header {
  display: flex;
  justify-content: space-between;
  padding: 0;
  border: 0;
  background-color: transparent;
  cursor: pointer;
}
.o-expandable_header:focus {
