  padding: 0.9375em;
  padding-top: 0;
}
.o-expandable__padded .o-expandable_content::before {
  content: '';
  display: block;
  border-top: 1px solid #b4b5b6;
  padding-top: 0.9375em;
}
.o-expandable__padded .o-expandable_content::after {
  padding-bottom: 0.9375em;
  width: 100%;
}
.o-expandable__background {
  background: #f7f8f9;
}
.o-expandable__border {
  border: 1px solid #b4b5b6;
}
.o-expandable-group .o-expandable__padded {
  border-bottom: 1px solid #b4b5b6;
}
.o-expandable-group .o-expandable__padded:first-child {
  border-top: 1px solid #b4b5b6;
}
@media print {
  .o-expandable_header[aria-expanded='true'] .o-expandable_cue-close,
  .o-expandable_header[aria-expanded='false'] .o-expandable_cue-open {
