/* ==========================================================================
   Design System
   Table Styling
   ========================================================================== */
.o-table_cell__right-align {
  text-align: right;
}
.o-table-wrapper__scrolling {
  box-sizing: border-box;
  overflow-y: hidden;
}
.o-table-wrapper__scrolling table {
  border: 1px solid #b4b5b6;
}
.o-table-wrapper__scrolling table > tbody > tr:nth-child(even) > th,
