  }
}
@media only screen and (max-width: 600px) {
  .o-table {
    width: 100%;
  }
  .o-table__striped tr:nth-child(even) > th,
  .o-table__striped tr:nth-child(even) > td {
    background: #ffffff;
  }
  .o-table__stack-on-small {
    border-top: 1px solid #b4b5b6;
  }
  .o-table__stack-on-small tr,
  .o-table__stack-on-small td,
  .o-table__stack-on-small [data-display-table='row'],
  .o-table__stack-on-small [data-display-table='cell'] {
    display: block;
  }
  .o-table__stack-on-small th,
