    padding-right: 0;
    padding-left: 0;
    width: 100%;
  }
  .o-table__stack-on-small > thead,
  .o-table__stack-on-small [data-display-table='thead'] {
    display: none;
  }
  .o-table__stack-on-small td[data-label]::before {
    margin-bottom: 1.07142857em;
    font-size: 0.875em;
    font-weight: 600;
    letter-spacing: 1px;
    line-height: 1.25;
    text-transform: uppercase;
    display: block;
    margin-top: 0;
    margin-bottom: 0.3125em;
    content: attr(data-label);
